<script lang="ts">
import { defineComponent, ref, computed, reactive } from "vue";
// import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";

import { LoginParams } from "@/apis/auth";
// import { Login } from "@/typings/store";

import logoImg from "@/assets/images/icons/png/bilab-black.png";
import bgImg from "@/assets/images/invite-confirm-background.jpeg";

const Component = defineComponent({
  name: "InviteConfirm",
  setup() {
    const route = useRoute();
    // const store = useStore();
    const router = useRouter();
    const step = ref(1);
    const key = computed(() => route.query.key);

    const componentStyles = ref({
      backgroundImage: `url("${bgImg}")`
    });

    console.log("key", key.value);

    const form = reactive({
      username: "",
      brand: "Nike",
      email: "",
      password: "",
      confirmPassword: ""
    });

    const onNextClick = () => {
      step.value = 2;
    };

    const onConfirmClick = async () => {
      await router.push({ name: "Root" });
    };

    return {
      step,
      form,
      componentStyles,

      onNextClick,
      onConfirmClick,

      logoImg
    };
  }
});

export default Component;
</script>

